.MainPart {
    top: 43px;
    border-radius: 2px;
    width: 775px;
    position: relative;
    margin-left: 5px;
    background-color: #e3f3ff;
    height: calc(100vh - 60px);
    left: 220px;
    padding-top: 0;
    padding-left: 0;
    font-size: 20px;
    margin-right: 5px;
    padding-bottom: 8px;
    overflow: scroll;
    overflow-x: hidden;
}
.TitleInMainPart {
    letter-spacing: normal;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 775px;
    border-bottom: 1px solid gray;
    height: 40px;
}
.TitleInMainPart__BlockForBtn {
    margin: 0 6px;
    cursor: pointer;
    padding: 4px;
}
.active {
    border-radius: 10px 10px 0 0;
    background-color: rgba(0, 0, 0, .4);
    border-bottom: 3px white solid;
}
.TitleInMainPart__Btn {
    height: 24px;
    top: 16px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    font-size: 20px;
}
.TitleInMainPart__Btn_Other {
    position: relative;
    top: -3px;
    height: 38px;
    width: 39px;
    margin-right: 8px;
    display: inline-block;
    & .active {
    }
}
.TitleInMainPart__Img_Other {
    height: 32px;
    width: 32px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.UserList {
    align-items: center;
    position: relative;
    display: flex;
    flex-flow: column;
}
.UserLine {
    display: flex;
    width: 736px;
    border-bottom: solid 1px lightgrey;
    border-top: solid 1px lightgrey;
    height: 45px;
    align-items: center;
    padding: 10px 0;
    .UserLine__Block_Avatar {
        height: 45px;
        position: relative;
        top: 2.5px;
        display: inline-block;
        cursor: pointer;
    }
    .UserLine__Block_Main {
        display: inline-block;
        width: 623px;
        top: 5px;
        left: 10px;
        position: relative;
    }
    .UserLine__Block_Main > div {
        font-size: 20px;
        position: relative;
        top: -4px;
        width: 623px;
        cursor: pointer;
        display: block;
        overflow: hidden;
    }
    .UserLine__Block_Img {
        display: block;
        cursor: pointer;
        width: 45px;
        height: 45px;
        border-radius: 50%;
    }
}
.ModalWindow {
    left: -20px;
    display: block;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .2);
    top: 0;
    z-index: 100;
}